
@import '../../../shared/utils-scss/mixins';
@import '../../../shared/utils-scss/variables';
@import '../../../shared/utils-scss/font.scss';
.phone {
  &__container {
     border: 1px solid $color-gray;
    width: 100%;
    height: 100%;


    // @include onTablet {
    //   width: 237px;
    //   height: 512px;
    // }

    // @include onDesktop {
    //   width: 272px;
    //   height: 506px;
    // }
  }
  &__wraper {
    padding: 32px 24px;
    transition: all 0.3s;
    
    &:hover {
      transform: scale(1.05);
    }
  }

  &__img {
    display: block;
    width: 148px;
    height: 129px;
    margin: 0 auto 8px;
    object-fit: contain;
    cursor: pointer;

    @include onTablet {
      width: 173px;
      height: 202px;
    }

    @include onDesktop {
      width: 208px;
      height: 196px;
    }
  }


  &__title {
    font-weight: 500;
    height: 58px;
    font-size: 14px;
    line-height: 21px;
    margin-bottom: 6px;
    width: 148px;
    color: $border-button-color-hover;
    @extend %mont-600;

    @include onTablet {
      width: 173px;
    }

    @include onDesktop {
      width: 208px;
    }
  }

  &__price {
    @extend %mont-800;
    font-size: 22px;
    line-height: 30px;
    color: $border-button-color-hover;

  }

  &__discount {
    font-size: 22px;
    line-height: 28px;
    text-decoration: line-through;
    color: $color-text-gray;
    @extend %mont-600;
  }

  &__line {
    display: block;
    width: 100%;
    border: 1px solid $color-text-gray;
    margin-bottom: 16px;
  }

  &__descriptin {
    display: flex;
    justify-content: space-between;
  }

  &__display {
    display: flex;
    flex-direction: column;
    gap: 8px;
    margin-bottom: 16px;
  }

  &__part {
    font-size: 12px;
    color: $color-text-gray;
    @extend %mont-600;
  }

  &__value {
    @extend %mont-600;
    font-size: 12px;
  }

  &__button {
    width: 176px;
    height: 40px;
    background-color: $border-button-color-hover;
    border: none;
    color: $color-white;
    font-size: 14px;
    cursor: pointer;
    @extend %mont-600;

    &:hover {
      box-shadow: 0 3px 13px 0;
    }
  }

  &__send {
    display: flex;
    gap: 8px;
    justify-content: center;
  }

  &__like {
    width: 38px;
    height: 38px;
    display: flex;
    align-items: center;
    justify-content: center;;
    border: 1px solid $border-button-color;
    cursor: pointer;

    &:hover {
      border-color: $border-button-color-hover;
    }
  }

  &__favorit {
    display: block;
    background-image:url(../../../shared/headerImg/Favourit.svg);
    background-repeat: no-repeat;
    background-position: center;
    width: 16px;
    height: 16px;
  }
}

.container {
  display: flex;
  gap: 8px;
  margin-bottom: 6.5px;
}

